import { Component } from '@angular/core';
import { Row } from './Row.model';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'experiment4';

  // THE ARRAY STORING THE DATA
  row_array: any = [
    new Row(1, 'Name 1', 'email 1', 1),
    new Row(2, 'Name 2', 'email 2', 2),
    new Row(3, 'Name 3', 'email 3', 3),
  ];


  // VARIABLES FOR STORING INPUTS FOR NAME, EMAIL, LEVEL
  var_up_id: number;
  var_up_name: string = "";
  var_up_email: string = "";
  var_up_level: number;


  // VARIABLES FOR DISABLING AND ENABLING INPUT FIELDS AND BUTTONS
  button_add: boolean =  true;
  button_update: boolean = false;
  button_cancel: boolean = false;

  add_name: boolean = true;
  add_email: boolean = true;
  add_level: boolean = true;

  update_name: boolean = false;
  update_email: boolean = false;
  update_level: boolean = false;


  // FUNCTION to add a new row
  RowAdd(input_name, input_email, input_level) {
    this.row_array.push(new Row(Date.now(), input_name, input_email, input_level));
  }

  // FUNCTION to delete a row
  RowDelete(id) {
    this.row_array = this.row_array.filter(item => {
      if(item.id !== id) { return item; };
    });
  }

  // FUNCTION to initiate editing of a row
  RowEdit(id: number, name: string, email: string, level: number) {
    this.button_add = false;
    this.button_update = true;
    this.button_cancel = true;

    this.add_name = false;
    this.add_email = false;
    this.add_level = false;

    this.update_name = true;
    this.update_email = true;
    this.update_level = true;

    this.var_up_name = name;
    this.var_up_email = email;
    this.var_up_level = level;

    this.var_up_id = id;
    this.var_up_name = name;
    this.var_up_email = email;
    this.var_up_level = level;
  }

  // FUNCTION to update a row
  RowUpdate(input_name, input_email, input_level) {
    this.row_array.map(item => {
      if(item.id === this.var_up_id) { 
        item['name'] = input_name; 
        item['email'] = input_email; 
        item['level'] = input_level; 
      };
    });
  }

  // FUNCTION to cancel row update
  RowUpdateCancel() {
    this.button_add = true;
    this.button_update = false;
    this.button_cancel = false;

    this.add_name = true;
    this.add_email = true;
    this.add_level = true;

    this.update_name = false;
    this.update_email = false;
    this.update_level = false;   
  }
}
